import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 用于切换菜单的闭合状态，false代表不闭合 true代表闭合
    collapsed: false,
  },
  mutations: {
    // 改变状态
    changeCollapsed(state) {
      state.collapsed = !this.state.collapsed;
    },
  },
  actions: {
    // 提交改变状态
    changeCollapsed({ commit }) {
      commit('changeCollapsed');
    },
  },
  modules: {},
});
